@import "animation.sass";
@import "mixin.sass";
@import "col-12.sass";

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }



.body {
    background: #0A091D;
    // overflow: hidden
 }    // width: 100%


a {
    text-decoration: none; }

.conteiner {
    max-width: 1280px;
    margin: 0 auto; }

.header__conteiner {
    display: flex;
    align-items: center;
    height: 80px;
    justify-content: space-between; }

.circle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 66px;
    height: 66px;
    background: #ffffff;
    border-radius: 200px;
    font-family: 'Hanalei Fill';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 31px;
    color: #000000; }

.nav-header {
    &__list {
        display: flex; }

    &__item {
        list-style: none;
        margin-right: 24px;
        &:last-child {
            margin-right: 0; } }
    &__link {
        position: relative;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
        cursor: pointer;
        &:after {
            content: "";
            display: block;
            position: absolute;
            right: 0;
            bottom: -3px;
            width: 0;
            height: 3px;
            background-color: #291DFF /* Цвет подчеркивания при исчезании линии*/;
            transition: width 0.5s /* Время эффекта */; }
        &:hover:after {
            content: "";
            width: 100%;
            display: block;
            position: absolute;
            left: 0;
            bottom: -3px;
            height: 3px /* Высота линии */;
            background-color: #291DFF /* Цвет подчеркивания при появлении линии*/;
            transition: width 0.5s  /* Время эффекта */; } } }
.nav-social {

    &__list {
        display: flex; }
    &__item {
        list-style: none;
        margin-right: 27px;
        &:last-child {
            margin-right: 0px; } }

    &__link {
        display: block;
        width: 24px;
        height: 24px; } }
.title {
    font-weight: 700;
    font-size: 48px;
    line-height: 59px;
    color: #FFFFFF; }

.gmail {
    background-image: url("../icons/gmail.svg"); }

