.btn-bottom {
    color: #ffffff;
    font-size: 100px;
    cursor: pointer; }

.section-hero {
    &__conteiner {
        position: relative;
        display: flex;
        height: 1024px;
        padding: 100px 0 133px 0; }

    &__coder {
        top: -187px;
        left: 95px; }
    &__wrapper {
        // display: flex
        padding-top: 30px; }


    &__title {
        @include animation-leftToRight;
        &-color {
            color: #D02222; } }
    &__text {
        margin-left: 40px;
        max-width: 390px;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 143.4%;
        color: #FFFFFF;
        margin-bottom: 30px; }
    &__avatar {
        position: relative;
        width: 600px;
        height: 600px;
        margin-right: 10%;
        &-img {
            position: absolute;
            z-index: 3;
            left: -52px;
            top: -70px;
            width: 600px;
            height: 600px;
            background-image: url("../img/Avatar.svg"); }
        &-triangle-red {
            position: absolute;
            z-index: 2;
            left: 0;
            top: 0;
            animation: 6s linear infinite rotate; }

        &-triangle-blue {
            position: absolute;
            z-index: 1;
            left: 0;
            top: 0;
            animation: 6s linear infinite rotate-blue; } }
    &__btn {
        margin-right: 34px;
        &-box {
            display: flex; } }
    &__background {
        position: absolute;
        top: 500px;
        left: 600px;
        &-box {
            position: relative; } } }


