.arrow-hero {
    margin-left: 30px; }

.arrow-1 {
    cursor: pointer;
    position: relative;
    width: 80px;
    height: 40px; }


.arrow-1 div {
    position: relative;
    top: 16px;
    width: 400px;
    height: 4px;
    background-color: #ffffff;
    display: block; }

.arrow-1 div::after {
    content: '';
    position: absolute;
    width: 20px;
    height: 4px;
    top: -6px;
    right: 384px;
    background-color: #ffffff;
    transform: rotate(135deg); }

.arrow-1 div::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 4px;
    top: 6px;
    right: 384px;
    background-color: #ffffff;
    transform: rotate(-135deg); }


.arrow-animation {
    position: relative;
    &:hover {
        animation: arrow-1 1s linear infinite; } }


.arrow-position-left {
    left: -120px; }


.arrow-3 div {
    position: relative;
    top: -24px;
    width: 400px;
    height: 4px;
    background-color: #ffffff;
    display: block; }

.arrow-3 div::after {
    content: '';
    position: absolute;
    width: 20px;
    height: 4px;
    top: -6px;
    right: -5px;
    background-color: #ffffff;
    transform: rotate(45deg); }

.arrow-3 div::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 4px;
    top: 6px;
    right: -5px;
    background-color: #ffffff;
    transform: rotate(-45deg); }


.arrow-color div {
    background: #D02222; }
.arrow-color div::after {
    background: #D02222; }
.arrow-color div::before {
    background: #D02222; }

